import React from 'react';
import '../Styles/Weekly.css';

import Weekcard from './Weekcard';

function Weekly ({
    weatherData,
    currentDate,
    weatherNameSetter,
    weeklyMobile,
    closeHourlyWeekly}) {

    let weekdays = weatherData.daily;
    let currentTime = currentDate.Datetime;
    let dayOfMonth = (currentTime[8]*10+currentTime[9]*1);
    let nextSevenDays = Array.from(weekdays).filter((item,index)=>{
        return index > 0;
    });

    let monNum = currentTime[5]*10+currentTime[6]*1;

    let month = 'January';
    function montChoose(monthNumber){
        let allMonths = ['Jan','Feb','Mar','Apr','May','Jun','Jul','Aug','Sep','Oct','Nov','Dec'];
        month = allMonths[monthNumber-1];
    }

    montChoose(monNum);

    let maxDays = 31;
    switch(true) {
        case (monNum===1||monNum===3||monNum===5||monNum===7||monNum===8||monNum===10||monNum===12):
            maxDays = 31;
            break;
        case (monNum===4||monNum===6||monNum===9||monNum===11):
            maxDays = 30;
            break;
        case (monNum===2 && Number(`${currentTime[0]}${currentTime[1]}${currentTime[2]}${currentTime[3]}`)%4===0):
            maxDays = 29;
            break;
        case (monNum===2):
            maxDays = 28;
            break;
        default: maxDays = 31;
    }

    return(
        <div className={`Weekly ${weeklyMobile==='open' ? 'show' : ''}`}>
            <div className="weekly-forecast">
                <p>Weekly Forecast</p>
                <img src={require('../Assets/icons/close_icon.svg').default}
                onClick={(event)=>closeHourlyWeekly(event)}
                alt=""/>
            </div>
            <div className="weekdays">
                {nextSevenDays.map((item,index)=>{
                    dayOfMonth = dayOfMonth+1;
                    if(dayOfMonth>maxDays){
                        dayOfMonth -= maxDays;
                        montChoose(monNum+1);
                    }
                    let dayName = 'Fail';
                    let timeDifference = (item.dt - 1578297600)/86400;
                    switch(true) {
                        case (timeDifference%7<1):
                            dayName = 'Mon'
                            break;
                        case (timeDifference%7<2):
                            dayName = 'Tue'
                            break;
                        case (timeDifference%7<3):
                            dayName = 'Wed'
                            break;
                        case (timeDifference%7<4):
                            dayName = 'Thu'
                            break;
                        case (timeDifference%7<5):
                            dayName = 'Fri'
                            break;
                        case (timeDifference%7<6):
                            dayName = 'Sat'
                            break;
                        case (timeDifference%7<7):
                            dayName = 'Sun'
                            break;
                        default: dayName = 'Mon'
                    }
                    let theDate = {month: month, day: dayOfMonth, dayName: dayName};
                    let weatherStateName = weatherNameSetter(item.weather[0].id);
                    return <Weekcard key={index} theDate={theDate} temps={item.feels_like} weatherStateName={weatherStateName}/>
                })}
            </div>
        </div>
    );
}

export default Weekly;