import React from 'react';
import '../Styles/Main.css';

import Searchbox from "./Searchbox";

function Main({
    weatherData,
    cityName,
    country,
    weatherNameSetter,
    getWeatherData,
    getCountry,
    setCityName,
    setHourlyMobile,
    setWeeklyMobile}) {

    let conditionIndex = weatherData.current.weather[0].id;
    let weatherStateName = weatherNameSetter(conditionIndex);

    let shortCountryName = country;
    if(country==='United States of America'){
      shortCountryName = 'USA';
    }
    else if(country.includes(' ')){
      let allWords = country.split(' ');
      shortCountryName = `${allWords[0]} ${allWords[1]} ${allWords.length>2 ? allWords[2] : ''}`;
    }

    function openHourlyWeekly(event) {
        if(event.target.name==="hourly"){
            setHourlyMobile('open');
            setWeeklyMobile('closed');
        }
        else{
            setWeeklyMobile('open');
            setHourlyMobile('closed');
        }
    }

    return(
        <div className={`Main ${weatherStateName}_bg`}>
            <Searchbox getWeatherData={getWeatherData} getCountry={getCountry} setCityName={setCityName}/> 
            <div className="top">
                <div className="weather-icon">
                    <img src={require(`../Assets/icons/${weatherStateName}_icon.svg`).default} alt=""/>
                </div>
                <div className="weather-condition">
                    <p className="it-is-text">Right now it's</p>
                    <p className="weather-type">{weatherStateName}</p>
                </div>
            </div>
            <div className="bottom">
                <div className="location">
                    <p>in {cityName}, <span>{shortCountryName}</span></p>
                    <p className="temperature">{Math.round(weatherData.current.temp)} °C</p>
                </div>
                <div className="weather-data">
                    <div className="single-data">
                        <p className="name">UV index</p>
                        <div className="spacer"></div>
                        <p className="value">{weatherData.current.uvi}</p>
                    </div>
                    <div className="single-data">
                        <p className="name">Humidity</p>
                        <div className="spacer"></div>
                        <p className="value">{weatherData.current.humidity}%</p>
                    </div>
                    <div className="single-data">
                        <p className="name">Wind speed</p>
                        <div className="spacer"></div>
                        <p className="value">{weatherData.current.wind_speed} m/s</p>
                    </div>
                    <div className="single-data">
                        <p className="name">Atmospheric pressure</p>
                        <div className="spacer"></div>
                        <p className="value">{weatherData.current.pressure/10} kPa</p>
                    </div>
                </div>
            </div>
            <button name="hourly" onClick={(event)=>openHourlyWeekly(event)}>Hourly</button>
            <button name="weekly" onClick={(event)=>openHourlyWeekly(event)}>Weekly</button>
        </div>
    );
}

export default Main;